.label:not(:last-child) {
  display: block;
  margin-bottom: 10px;
}

.fieldset:not(:last-child) {
  margin-bottom: 20px;
}

.loveCatsNo {
  display: inline-block;
  margin-bottom: 10px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  white-space: nowrap;
}
